import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Button } from 'antd';
import { TableData, CartHeadAction } from 'components/common';
import { BtnExport } from 'components/common/button';
import routeConstant from 'routes/route/routeConstant';
import {
  getStudentsForPartyMem,
  updatePoliticalDate,
  exportPoliticStudents,
} from 'services/studentForPartyMem';
import { Link } from 'react-router-dom';
import { getQueryParams } from 'utils/urlUtil';
import { dateToString } from 'utils/formatUtil';
import FormSearchStuForPartyMem from './FormSearchStuForPartyMem';
import './style.scss';

class StuPartyMemPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleKhaiTru = async ({ id, politicalDate }) => {
    try {
      await updatePoliticalDate({ id, politicalDate });
      this.setState({ reload: this.state.reload + 1 });
    } catch (error) {
      console.log(error);
    }
  };

  handleExport = async ({
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    studentCode,
    studentFullName,
  }) => {
    try {
      await exportPoliticStudents({
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        classId,
        studentCode,
        studentFullName,
      });
    } catch (error) {
      console.log(error);
    }
  };

  columns = [
    {
      title: 'Mã SV',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'Họ tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Giới tính',
      dataIndex: 'genderText',
      key: 'genderText',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'Chuyên ngành',
      dataIndex: 'studyFieldName',
      key: 'studyFieldName',
    },
    {
      title: 'Khóa học',
      dataIndex: 'courseName',
      key: 'courseName',
    },
    {
      title: 'Tên khoa',
      dataIndex: 'facultyName',
      key: 'facultyName',
    },
    {
      title: 'Tên hệ',
      dataIndex: 'trainTypeName',
      key: 'trainTypeName',
    },
    {
      title: 'Ngày kết nạp',
      dataIndex: 'politicalDate',
      render: (text, record) => {
        if (record.politicalDate == null) {
          return <Fragment />;
        }
        return <Fragment>{dateToString(record.politicalDate, 'DD/MM/YYYY')}</Fragment>;
      },
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => {
        if (record.politicalDate) {
          return (
            <Fragment>
              <Button
                className="btnEventStuPartyMem btnKhaiTru"
                type="primary"
                onClick={() => this.handleKhaiTru({ id: record.id, politicalDate: '' })}
              >
                Khai trừ
              </Button>
            </Fragment>
          );
        }
        return (
          <Link
            to={{
              pathname: routeConstant.DOAN_HOI__DANG_VIEN__NHAP_DU_LIEU,
              search: `?id=${record.id}`,
            }}
          >
            <Button className="btnEventStuPartyMem btnKetNap" type="primary">
              Kết nạp
            </Button>
          </Link>
        );
      },
    },
  ];

  render() {
    const { paramSearch } = this.state;
    return (
      <Card title="Đảng viên là sinh viên">
        <CartHeadAction>
          <BtnExport
            title="Xuất Excel"
            onClick={() =>
              this.handleExport({
                trainTypeId: paramSearch.trainTypeId,
                facultyId: paramSearch.facultyId,
                courseId: paramSearch.courseId,
                studyFieldId: paramSearch.studyFieldId,
                classId: paramSearch.classId,
                studentCode: paramSearch.studentCode,
                studentFullName: paramSearch.studentFullName,
              })
            }
          />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStuForPartyMem />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getStudentsForPartyMem}
              allowGetData={
                !!paramSearch.trainTypeId &&
                !!paramSearch.facultyId &&
                !!paramSearch.courseId &&
                !!paramSearch.studyFieldId
              }
              columns={this.columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StuPartyMemPage />
  </Page>
);



